.take-order__contacts {
  padding-bottom: 76px;
}
.take-order__contacts-title {
  @include font(18px, 700, 23px);
  color: var(--accent-color);
  text-transform: uppercase;

  margin-bottom: 28px;
}

.take-order__form {
  display: flex;
  flex-direction: column;
}
.take-order__form-field {
  &:nth-child(-n + 2) {
    margin-bottom: 44px;
  }
  @include font(16px, 400, 20px);
  color: var(--text-color);
}

.take-order__form-input {
  width: 100%;

  border: none;
  border-bottom: 1px solid #487996;

  &::placeholder {
    @include font(16px, 400, 20px);
    text-transform: capitalize;
    color: var(--text-color);
  }
}

.take-order__form-list {
  margin-bottom: 44px;
  @include font(16px, 400, 20px);
  color: var(--text-color);

  border: none;
  border-bottom: 1px solid #487996;
  appearance: none;

  background-image: url(../images/mobile/take-over/polygon-open.svg);
  background-repeat: no-repeat;
  background-position: right;
  &:hover,
  &:focus {
    background-image: url(../images/mobile/take-over/polygon-close.svg);
  }
}

.take-order__message {
  width: 100%;
  height: 199px;
  margin-top: 4px;
  padding: 22px 20px;
  border: 1px solid #487996;
  box-sizing: border-box;
  border-radius: 50px 0px;
  resize: none;

  @include font(16px, 400, 20px);
  color: var(--text-color);
}

// добавить opacity и вынести в "скрепку"?
.take-order__message-input {
  color: transparent;
}
.take-order__message-text {
  @include font(16px, 400, 20px);
  color: var(--form-placeholder-color);
}

.take-order__btn {
  margin-top: 24px;
  margin-bottom: 52px;
}

.take-order__contact-wrapper:not(:last-child) {
  @media screen and (max-width: 767px) {
    margin-bottom: 24px;
  }
}
.take-order__contact-data-text {
  @include font(18px, 700, 23px);

  color: var(--accent-color);

  margin-bottom: 8px;
}

.take-order__contact-data-list {
  &:not(:last-child) {
    margin-bottom: 24px;
  }
}

.take-order__contact-data-item {
  &:not(:last-child) {
    margin-bottom: 8px;
  }
}

.take-order__contact-data-link,
.take-order__contact-data-item {
  @include font(16px, 400, 20px);
  text-transform: capitalize;

  color: var(--text-color);
}

.take-order__contact-data-item {
  display: flex;
  flex-direction: column;
}

// 768+

@media screen and (min-width: 768px) {
  .take-order__contacts-title {
    text-align: center;
    @include font(24px, 700, 30px);
  }

  .take-order__form {
    margin: 0 auto;
    width: 420px;
  }
  .take-order__btn {
    margin-left: auto;
    margin-top: 44px;
    margin-bottom: 76px;
  }
  .take-order__contact-data {
    display: flex;
    justify-content: space-between;
  }
  .take-order__contact-data-text {
    text-align: start;
    @include font(24px, 700, 30px);
  }
  .take-order__day {
    margin-left: auto;
  }
  .take-order__time {
    margin-left: auto;
  }
}

// 1444+
@media screen and (min-width: 1444px) {
  .take-order__container {
    display: flex;
    justify-content: space-between;
  }

  .take-order__contacts-title {
    @include font(36px, 700, 45px);
    text-align: start;
  }

  .take-order__form-input {
    &::placeholder {
      @include font(18px, 400, 20px);
    }
  }

  .take-order__contact-data {
    display: block;
  }

  .take-order__contact-wrapper {
    text-align: end;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  .take-order__contact-data-text {
    margin-bottom: 18px;
    text-align: end;
  }

  .take-order__contact-data-text {
    @include font(36px, 700, 45px);
  }

  .take-order__contact-data-item {
    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }
  .take-order__contact-data-link {
    @include font(18px, 400, 20px);
  }
  .take-order__contact-data-item {
    display: block;
    @include font(18px, 400, 20px);
  }
}
